let loadCss = function (file) {
  let fileRef = document.createElement("link");

  fileRef.setAttribute("rel", "stylesheet");
  fileRef.setAttribute("href", file);
  document.getElementsByTagName("head")[0].appendChild(fileRef);
};

let loadSlickCss = function() {
  loadCss("css/slick.min.css");
};

let loadRevealatorCss =function() {
  loadCss("css/fm.revealator.jquery.min.css");
};

loadSlickCss();
loadRevealatorCss();

let slider = document.querySelector(".reviews__btn-wrapper");

$('.reviews__wrapper').slick({
  arrows: true,
  prevArrow: slider.querySelector(".slider__btn--prev"),
  nextArrow: slider.querySelector(".slider__btn--next"),
  infinite: true,
  speed: 300,
  slidesToShow: 1,
  centerMode: true,
  variableWidth: true
});

let toggleMenu = function () {
  let navMain = document.querySelector(".page-header");
  let navToggle = navMain.querySelector(".main-nav__toggle");
  if (!navMain || !navToggle) return;
  navMain.classList.remove("page-header--nojs");

  navToggle.addEventListener("click", function() {
    navMain.classList.toggle("page-header--menu-opened");
  });
};

toggleMenu();

let modalInitiate = function() {
  let link = document.querySelector(".button-js");
  let popup = document.querySelector(".modal");
  let overlay = document.querySelector(".overlay");
  let close = document.querySelector(".modal__close-btn");
  let lastFocus;

  if (!link || !popup || !overlay || !close) return;

  let modalShow = function() {
    popup.classList.add("modal--show");
    overlay.classList.add("overlay--show");
    lastFocus = document.activeElement;
    popup.setAttribute("tabindex", "0");
    popup.focus();
  };

  let modalClose = function() {
    popup.classList.remove("modal--show");
    overlay.classList.remove("overlay--show");
    lastFocus.focus();
  };

  link.addEventListener("click", function(event) {
    event.preventDefault();
    modalShow();
  });

  close.addEventListener("click", function(event) {
    event.preventDefault();
    modalClose();
  });

  overlay.addEventListener("click", function(event) {
    event.preventDefault();
    modalClose();
  });

  document.addEventListener("keyup", function(event) {
    if (event.defaultPrevented) {
      return;
    }

    var key = event.keyCode;

    if (key === "Escape" || key === "Esc" || key === 27) {
      event.preventDefault();
      if (popup.classList.contains("modal--show")) {
        modalClose();
      }
    }
  });
};

modalInitiate();

